.elements {
    width: 1270px;
    margin: 0 auto;
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
    justify-content: center;
    height: 100%;
}

.сatalog__title {
    font-size: 35px;
    font-weight: 900;
    margin: 10px auto;
    text-align: center;
}