.App {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

h3 {
    text-align: center;
}

.image_2 {
    width: 50px;
}

.topping {
    margin-top: 0.3rem;
    vertical-align: text-bottom;
}

.result {
    margin-top: 1rem;
}

.toppings-list,
.total {
    width: 30%;
    margin: 0 auto;
}

.toppings-list {
    list-style: none;
    padding: 0;
}

.toppings-list li {
    margin-bottom: 0.5rem;
}

.toppings-list-item {
    display: flex;
    justify-content: space-between;
}

.toppings-list li:last-child {
    border-top: 1px solid #ccc;
    margin-top: 1rem;
    padding-top: 1rem;
}

.toppings-list-item label {
    vertical-align: text-bottom;
    margin-left: 0.2rem;
}

.total {
    margin-top: 1rem;
}

@media screen and (max-width: 600px) {

    .toppings-list,
    .total {
        width: 90%;
    }
}